// @flow
import * as React from "react"

type Props = {
    name?: String,
    children: React.Node
}

class LayoutContent extends React.Component<Props> {

    static defaultProps = {
        name: "main"
    };

    render()
    {
        const { name, children } = this.props;

        return (
            <div id={ name } >
                {
                    children
                }
            </div>
        );
    }
}


export default LayoutContent
